const getJunkForm = (start, end, site_name) => {
  axios({
    url:
      "/api/dump-record/site_data_day/" + start + "/" + end + "/" + site_name,

    method: "get",
    // responseType:'blob'
    responseType: "blob",
  }).then(function (res) {
    if (res.status == 200) {
      console.log("");
      // blob 
      let blob = new Blob([res.data], { type: res.type });
      // 
      let filename = res.headers["content-disposition"];
      filename = decodeURIComponent(filename.split("filename=")[1]);
      //  a 
      let downloadElement = document.createElement("a");
      let href = window.URL.createObjectURL(blob); //
      downloadElement.href = href;
      downloadElement.download = filename; //
      document.body.appendChild(downloadElement); // a
      downloadElement.click(); //
      document.body.removeChild(downloadElement); //
      window.URL.revokeObjectURL(href); //blob
    }
  });
};

