  { icon: "03,17", title: "", to: "ljfs", submenu: [] },
  { icon: "01,16", title: "", to: "carRecord", submenu: [] },
  { icon: "03,09", title: "", to: "carWarning", submenu: [] },
  { icon: "03,10", title: "", to: "osmoticFluid", submenu: [] },
  { icon: "02,14", title: "", to: "alarmEvent", submenu: [] },
];
function displayContent(name) {
  router.push({ name });
}
function getIcon(idxStr) {
  const len = -30;
  const x = parseInt(idxStr.split(",")[1] - 1) * len;
  const y = parseInt(idxStr.split(",")[0] - 1) * len;
  return {
    backgroundPositionX: x + "px",
    backgroundPositionY: y + "px",
  };
}
